
.calculator {
    margin: auto;
    width: 250px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-left: 10px;
  }
  
  input[type='text'] {
    width: 100%;
    margin-bottom: 10px;
    padding: 10px;
    font-size: 18px;
  }
  
  .keypad button {
    width: 50px;
    height: 50px;
    margin: 5px;
    font-size: 18px;
    border: 1px solid #ccc;
    border-radius: 5px;
    cursor: pointer;
    margin-left: 10px;
  }
  body{
    background-color: whitesmoke;
  }
  
 
  .keypad button:hover {
    background-color: black;

  }
 
  .p{
    font-size: 50px;
  }